    min-width: 121px;
    font-size: 14px;
    line-height: 1.21;
  }
}

.button:hover, .button:focus {
  background-color: #0a8817;
  border-color: #0a8817;
  text-decoration: none;
}

.button:focus {
  color: #fcfcfc;
}

.button.disabled, .button:disabled {
  pointer-events: none;
  cursor: default;
  background-color: #ababab;
  border-color: #ababab;
}

.backdrop {
  width: 100%;
  height: 100%;
  text-align: center;
  z-index: 100;
  background: rgba(176, 196, 167, .5);
  padding: 10px;
  line-height: calc(100vh - 20px);
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  vertical-align: middle;
  width: 100%;
  max-width: 280px;
  background: var(--first-text-color);
  border: 2px solid var(--yellow);
  text-align: left;
  border-radius: 10px;
  padding: 28px;
  font-size: 14px;
  line-height: 1.43;
  transition: transform .35s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
  position: relative;
  transform: translate(0)scale(1);
  box-shadow: 0 15px 15px 15px rgba(0, 0, 0, .12), 0 13px 13px 13px rgba(0, 0, 0, .12), 0 13px 13px 13px rgba(0, 0, 0, .12);
}

@media screen and (min-width: 768px) {
  .modal {
    max-width: 568px;
    padding: 32px 58px;
    font-size: 16px;
    line-height: 1.4;
  }
}

.is-hidden .modal {
  transform: translate(-50%, -50%)scale(0);
}

.modal__close {
  width: 20px;
  height: 20px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
